import React from 'react'

import {icons} from '../icons'

const Skills = props => {

    const links = (input) => { 
        return Object.values(icons[input])
    }

    const keys = (input) => {
        return Object.keys(icons[input])
    }

    
    const getMap = (input) => {
        return links(input).map((i, index) => (
            <div className="skill-icon-container" key={index}> 
                <img className="skill-icon"  src={i}/>
        <div className="skill-description"> {keys(input)[index]} </div>
            </div>
        ))
    }

    return (
        <div 
            className="page" 
            id="skills"
        >
            <div className="container">
                <div className="page-title">SKILLS</div>
                <div 
                    className="bar" 
                    style={{
                        marginTop: "20px",
                        marginBottom: "10px"
                    }}
                />

                <div className="skill-wrap" >
                    <div className="skill-container fade-in">
                        <div className="skill-title">DEVELOPMENT</div>
                        <div className="skill-icon-wrap">
                            {getMap("tech")}
                        </div>
                    </div>
                    <div className="bar"/>
                    <div className="skill-container fade-in">
                        <div className="skill-title">LANGUAGES</div>
                        <div className="skill-icon-wrap">
                            {getMap("language")}
                        </div>
                    </div>
                    <div className="bar"/>
                    <div className="skill-container fade-in">
                        <div className="skill-title">UI / UX DESIGN</div>
                        <div className="skill-icon-wrap">
                            {getMap("audioDesign")}
                        </div>
                    </div>
                    <div className="bar"/>
                    <div className="skill-container fade-in">
                        <div className="skill-title">DIGITAL MARKETING</div>
                        <div className="skill-icon-wrap">
                            {getMap("advertising")}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    
    )
}

export default Skills